import React from "react";
import PropTypes from 'prop-types';

import {
	Gear, Download
} from 'react-bootstrap-icons';

const FormDataBar = (props: { name: string , status : string }) => {
		const onClickDownload = () => {
			alert(`Start download form: ${props.name} `)
		}
		const onClickGear = () => {
			alert(`Open settings menu for form: ${props.name}`)
		}

    return(
        <div className='data-bar'>
            <table border={0} style={{ width: '95%',borderStyle:'none', marginBottom:'1rem', borderCollapse:'collapse'}}>
                <tr>
                    <td style={{fontSize:'18px',borderStyle:'none', width:'50%', textAlign:'left',borderCollapse:'collapse'}}>
                        <span style={{fontSize: '1.5em'}}>
													{props.name}
												</span>
                    </td>
                    <td style={{fontSize:'18px',borderStyle:'none', width:'35%', textAlign:'left'}}>
												<span style={{fontSize: '1.5em'}}>
													{props.status}
												</span>
                    </td>
                    <td style={{fontSize:'18px',borderStyle:'none', width:'15%',textAlign:'right'}}>
												<span style={{fontSize: '1.4em'}}>
													&nbsp;
													<Download onClick={() => onClickDownload()}/>
													&nbsp;&nbsp;&nbsp;&nbsp;
													<Gear onClick={() => onClickGear()}/>
												</span>
                    </td>
                </tr>
            </table>
        </div>)
}

FormDataBar.propTypes = {
		name: PropTypes.string,
    status: PropTypes.string,
};

export default FormDataBar
