import React, { useState } from 'react';
import PageHeader from './page-layout-components/page-header/PageHeader';

import riskScore from '../temp_data/img/risk_score1.png';
// import netGraphPlaceholder from '../temp_data/img/net_graph_simple.png';
import postureMaturityPlaceholder from '../temp_data/img/posture_maturity.png';

import Topo from './page-layout-components/topo/Topo';

const NetworkTopology = () => {
	const [isPostureShow, setPostureShow] = useState(false)

	const togglePostureShow = () => {
		setPostureShow(prev => {return !prev})
	}

	return (
		<>
			<PageHeader text='Enterprise architecture overview' />

			<span style={{float:'left', textAlign: 'left', marginLeft: '2rem'}}>
				<strong>
					<h5>Topology</h5>
				</strong>
			</span>

			<span style={{float:'right', textAlign: 'right', marginRight: '25px'}}>
				<strong>
					<img src={riskScore} />
					&nbsp;
					Risk score
				</strong>
			</span>
			<br></br>
			

			{!isPostureShow?
			(<Topo width={'70vw'} height={'60vh'}/>)
			: (<Topo width={'50vw'} height={'40vh'}/>)}

			<div>
				<div style={{display : 'block'}}>
					<center>

						<button onClick={()=> togglePostureShow() }>
							 {isPostureShow ? 'HIDE' : 'SHOW'}
						</button>
					</center>

					{/* <span style={{float:'left', textAlign: 'left', marginLeft: '25px'}}>
						<strong>Posture maturity</strong>
					</span> */}
				</div>

				{isPostureShow ?
				(<img src={postureMaturityPlaceholder} style={{margin:'auto'}} width={'80%'} />)
				: null
				}
			</div>
		</>
	)
}

export default NetworkTopology;
